<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" app clipped color="drawer">
      <v-list dense rounded>
        <v-list-item
          v-for="item in items"
          :key="item.text"
          @click="item.fun"
          :to="item.path"
          link
        >
          <v-list-item-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-item-action>

          <v-list-item-content>
            <v-list-item-title>
              {{ item.text }}
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
      <template v-slot:append>
        <div class="pa-2">
          <v-list dense rounded>
            <v-list-item
              v-for="item in extras"
              :key="item.text"
              @click="item.fun"
              :to="item.path"
              link
            >
              <v-list-item-action>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-item-action>

              <v-list-item-content>
                <v-list-item-title>
                  {{ item.text }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </div>
      </template>

      <v-dialog v-model="dialog" max-width="310">
        <v-card>
          <v-card-title class="headline">
            ¿Quieres salir de la aplicación?
          </v-card-title>

          <v-card-text>
            Si te sales deberás volver a loguearte para acceder a la aplicación
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>

            <v-btn color="grey" text @click="dialog = false"> Cancelar </v-btn>

            <v-btn
              color="red"
              text
              @click="dialog = false"
              :to="{ path: 'login' }"
            >
              Salir
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-navigation-drawer>

    <!--color de la barra superior-->
    <v-app-bar app clipped-left color="mainlayout" dense>
      <!-- Es para esconder la barra lateral (son las tres rayas) -->
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />

      <v-icon class="mx-4" large> mdi-hospital-box-outline </v-icon>

      <v-toolbar-title class="mr-12 align-center">
        <span class="title">SALUDAPP </span>
      </v-toolbar-title>

      <v-spacer />

      <!--tamaño de la barra de busqueda-->
      <v-row align="center" style="max-width: 200px">
        <div class="my-2">
          <v-btn  :to="{ path: 'perfil' }" fab x-small>
            <v-icon>mdi-account-circle</v-icon>
          </v-btn>
        </div>
        <v-spacer />
        Borja Alvarez Miguelez
      </v-row>
    </v-app-bar>

    <v-parallax class="fill-height" height="100%">
      <v-container>
        <router-view></router-view>
      </v-container>
    </v-parallax>

    <v-footer color="mainlayout">
      <v-card-text class=".font-italic font-weight-bold text-center">
        &copy;2021 — <strong>SALUDAPP</strong>
      </v-card-text>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  props: {
    source: String,
    username: String,
  },
  data() {
    return {
      drawer: null,
      dialog: false,
      items: [
        {
          icon: "mdi-calendar-range",
          text: "Pedir Cita",
          path: "pedircita",
          fun: () => {},
        },

        {
          icon: "mdi-history",
          text: "Historial de Citas",
          path: "historialcitas",
          fun: () => {},
        },

        {
          icon: "mdi-water-outline",
          text: "Análisis",
          path: "analisis",
          fun: () => {},
        },

        {
          icon: "mdi-radiology-box-outline",
          text: "Pruebas",
          path: "pruebas",
          fun: () => {},
        },

        {
          icon: "mdi-account-box-outline",
          text: "Perfil",
          path: "perfil",
          fun: () => {},
        },
      ],
      extras: [
        {
          icon: "mdi-cog",
          text: "Configuración",
          path: "configuracion",
          fun: () => {},
        },
        { icon: "mdi-help-box", text: "Ayuda", path: "ayuda", fun: () => {} },
        {
          icon: "mdi-exit-run ",
          text: "Salir",
          path: null,
          fun: () => {
            this.dialog = true;
          },
        },
      ],
    };
  },
  created() {
    //this.$vuetify.theme.dark = true;
  },
};
</script>
